* {
  margin: 0;
  padding: 0;
}
@keyframes background {
  100% {
    background-size: 4000px 2000px;
  }
}
body {
  font-family: "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  background-color: #092964;
  background-image: url("low-poly-grid-haikei-2.svg");
  background-size: 3000px 1000px;
  background-position: -500px 0;
  animation: background 50s infinite alternate linear;
}
h1 {
  color: aliceblue;
  letter-spacing: 2px;
}
ul {
  text-decoration: none;
  list-style-type: none;
}
li {
  text-decoration: none;
}

.todo__title-wrapper {
  margin: 20px auto;
}
.todo__form-input-task-wrapper {
  display: flex;
  justify-content: center;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 12px;
}
.todo__input-task-wrapper > input {
  background-color: aliceblue;
  border-radius: 10px;
  border: 1px solid #09063d;
  padding: 8px 0 8px 0;
  outline: none;
  max-width: 418px;
  width: 100%;
  font-size: 21px;
  transition: ease-in-out 0.5s;
  color: aliceblue;
  text-align: center;
}
.todo__input-task-wrapper > input::placeholder {
  font-size: 21px;
  font-weight: 800;
  letter-spacing: 1px;
}
.todo__input-task-wrapper > input::placeholder {
  color: black;
  letter-spacing: 1px;
}
.todo__input-task-wrapper > input:focus::placeholder {
  color: transparent;
}
.todo__input-task-wrapper > input:focus {
  background-color: rgb(88, 124, 197);
  box-shadow: rgba(0, 0, 0, 0.35) 0px -50px 36px -28px inset;
  transition: 0.5s;
}
.todo__input-task-submit > button {
  font-size: 21px;
  font-weight: 800;
  letter-spacing: 1px;
  border: none;
  border-radius: 10px;
  max-width: 418px;
  width: 100%;
  padding: 9px 10px 9px 10px;
  background-color: aliceblue;
  cursor: pointer;
  transition: ease-in-out 0.5s;
  color: black;
}
.todo__input-task-submit > button:hover {
  background-color: rgb(88, 124, 197);
  box-shadow: rgba(0, 0, 0, 0.35) 0px -50px 36px -28px inset;
  color: aliceblue;
  transition: 0.5s;
}
.todo__list-wrapper {
  margin: 0 auto;
  max-width: 418px;
  min-height: 330px;

  width: 100%;
  height: 100%;
  background-color: aliceblue;
  border-radius: 10px;
}
.todo__list-case {
  position: relative;
  padding-top: 12px;
  padding-bottom: 12px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.todo__title-wrapper,
.text-empty-list,
.todo__list-wrapper,
.todo__form-input-task-wrapper,
.wrapper-buttons {
  -webkit-animation: fadeIn 2.5s;
  animation: fadeIn 2.5s;
}
.text-completed {
  color: aliceblue;
  margin: 20px auto 20px;
}

.text-completed {
  -webkit-animation: fadeIn 2.5s;
  animation: fadeIn 2.5s;
}
@keyframes entrence {
  from {
    transform: scale(0, 0);
  }
  to {
    transform: scale(1, 1);
  }
}
.class-for-dissapear {
  -webkit-animation: dissapear 2.5s;
  animation: dissapear 2.5s;
}
@keyframes dissapear {
  from {
    transform: scale(1, 1);
  }
  to {
    transform: scale(0, 0);
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@-webkit-keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.todo__task-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 18px;
  font-weight: 500;
  border: 1px solid transparent;
  margin: 0 auto;
  padding: 6px;
  width: 92%;
  border-radius: 6px;
  background-color: rgb(88, 124, 197);
  color: rgb(251, 251, 251);
  box-shadow: rgba(0, 0, 0, 0.35) 0px -50px 36px -28px inset;
  letter-spacing: 2px;
}

.text-empty-list {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40vh;
}

.removed {
  -webkit-animation: fadeOut 2s;
  animation: fadeOut 2s;
}
@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@-webkit-keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@-webkit-keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.icon-case-item-task {
  display: flex;
  align-items: center;
}
.icons-case-remove-done {
  display: flex;
  align-items: center;
  gap: 12px;
}

.wrapper-buttons {
  margin: 0 auto 12px;
  max-width: 418px;
  display: flex;
  justify-content: space-between;
}

@media (max-width: 576px) {
  h1 {
    font-size: 26px;
  }
  .todo__input-task-wrapper > input,
  .todo__input-task-wrapper > input::placeholder,
  .todo__input-task-submit > button {
    font-size: 18px;
  }
}
