.button {
  width: 48%;
  height: 42px;
  cursor: pointer;
  background-color: aliceblue;
  font-size: 21px;
  padding: 8px 0 8px 0;
  border: none;
  border-radius: 10px;
  font-weight: 600;
  animation-name: entrence;
  animation-duration: 3s;
  color: black;
}

@keyframes entrence {
  from {
    height: 0;
    font-size: 0;
    padding: 0;
  }
  to {
    height: 42px;
    font-size: 21px;
    padding: 8px 0 8px 0;
  }
}
@-webkit-keyframes entrence {
  from {
    height: 0;
    font-size: 0;
    padding: 0;
  }
  to {
    height: 42px;
    font-size: 21px;
    padding: 8px 0 8px 0;
  }
}
@keyframes entrence {
  from {
    height: 0;
    font-size: 0;
    padding: 0;
  }
  to {
    height: 42px;
    font-size: 21px;
    padding: 8px 0 8px 0;
  }
}
@-webkit-keyframes entrence {
  from {
    height: 0;
    font-size: 0;
    padding: 0;
  }
  to {
    height: 42px;
    font-size: 21px;
    padding: 8px 0 8px 0;
  }
}

.button:hover {
  background-color: rgb(88, 124, 197);
  box-shadow: rgba(0, 0, 0, 0.35) 0px -50px 36px -28px inset;
  color: aliceblue;
  transition: 0.5s;
}

@media (max-width: 576px) {
  .button {
    font-size: 20px;
    animation-name: entrence;
    animation-duration: 3s;
  }
  @keyframes entrence {
    from {
      height: 0;
      font-size: 0;
      padding: 0;
    }
    to {
      height: 42px;
      font-size: 20px;
      padding: 8px 0 8px 0;
    }
  }
  @-webkit-keyframes entrence {
    from {
      height: 0;
      font-size: 0;
      padding: 0;
    }
    to {
      height: 42px;
      font-size: 20px;
      padding: 8px 0 8px 0;
    }
  }
  @keyframes entrence {
    from {
      height: 0;
      font-size: 0;
      padding: 0;
    }
    to {
      height: 42px;
      font-size: 20px;
      padding: 8px 0 8px 0;
    }
  }
  @-webkit-keyframes entrence {
    from {
      height: 0;
      font-size: 0;
      padding: 0;
    }
    to {
      height: 42px;
      font-size: 20px;
      padding: 8px 0 8px 0;
    }
  }
}
