.todo {
  display: flex;
  align-items: center;
  padding: 10px 20px;
  margin: 15px 0;
  font-size: 1.5rem;
  border-radius: 5px;
  border: 2px solid #555;
  color: #112d49;
  background-color: #fbfef9;
}

.todoText {
  width: 100%;
  text-align: left;
}

.todoIcon {
  font-size: 1.8rem;
  margin-right: 10px;
}

.completedTodo {
  background-color: unset !important;
  color: gray !important;
  transition: 0.5s;
}

.todo.completedTodo .todoIcon,
.todo.completedTodo .checkIcon,
.todo.completedTodo .deleteIcon {
  color: gray;
}

.checkIcon,
.deleteIcon {
  cursor: pointer;
  font-size: 1.6rem;
  transition: transform 0.3s;
}

.checkIcon:hover,
.deleteIcon:hover {
  cursor: pointer;
  transform: scale(1.3);
}

.checkIcon:hover {
  color: rgb(36, 154, 36);
}

.deleteIcon:hover {
  color: rgb(221, 40, 40);
}

.todoTaskItem {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 18px;
  font-weight: 500;
  border: 1px solid transparent;
  margin: 0 auto;
  padding: 6px;
  width: 92%;
  border-radius: 6px;
  background-color: rgb(88, 124, 197);
  color: rgb(251, 251, 251);
  box-shadow: rgba(0, 0, 0, 0.35) 0px -50px 36px -28px inset;
  letter-spacing: 1px;
  -webkit-animation: entrence 2s;
  animation: entrence 2.5s;
  transition: 2.5s;
}

@keyframes entrence {
  from {
    transform: scale(0, 0);
  }
  to {
    transform: scale(1, 1);
  }
}

