@media (max-width: 576px) {
  .todoTaskItem {
    font-size: 16px;
    letter-spacing: 0;
  }
  .checkIcon,
  .deleteIcon {
    font-size: 1.4rem;
  }
  .todoIcon {
    font-size: 1.4rem;
    margin-right: 4px;
  }
}
